// Check mobiles
function is_mobile() {return (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent));}
// Check OSX
function is_OSX() {return navigator.platform.match(/(Mac|iPhone|iPod|iPad|Android)/i) ? true : false;}
// Plural
function si_plural(number, titles){cases = [2, 0, 1, 1, 1, 2]; return titles[ (number%100>4 && number%100<20)? 2 : cases[(number%10<5)?number%10:5] ];} 
// SI False
$('.si-false').click(function(){return false;});

// Preload
$.fn.preload = function() {this.each(function(){$('<img/>')[0].src = this;});};
// Reverse
$.fn.reverse = [].reverse;


